import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import { vModelMixin } from '../common/vue3/adapter';


export default {
  name: 'PressMessageBoardInput',
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  mixins: [vModelMixin],
  props: {
    sendBtnEnable: {
      type: Boolean,
      default: false,
    },
    // inputBottom: {
    //   type: [Number, String],
    //   default: 0,
    // },
    placeholder: {
      type: String,
      default: '说点什么...',
    },
    textAreaIsAutoHeight: {
      type: [Boolean, String],
      default: false,
    },
    adjustPosition: {
      type: Boolean,
      default: false,
    },
    textAreaIsFocus: {
      type: Boolean,
      default: false,
    },
    showConfirmBar: {
      type: Boolean,
      default: false,
    },
    sendBtnText: {
      type: String,
      default: '发送',
    },
    confirmType: {
      type: String,
      default: 'send',
    },
    ...defaultProps,
  },
  emits: [
    'input',
    'update:modelValue',
    'clickInput',
    'sendMsg',
    'blur',
    'focus',
  ],
  data() {
    return {
      inputBottom: 0,
    };
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    onInput(e) {
      const value = e.target.value || e.detail.value;
      this.emitModelValue(value);
    },
    clickInput() {
      this.$emit('clickInput');
    },
    sendMsg() {
      this.$emit('sendMsg', this.realModelValue);
    },
    keyboardheightchange(res) {
      const keyHeight = res.detail && res.detail.height;
      console.info('[keyboardheightchange] res: ', res);
      this.inputBottom = `${keyHeight > 0 ? keyHeight : 0}`;
    },
    onBlur() {
      this.inputBottom = 0;
      this.$emit('blur');
    },
    onFocus() {
      this.$emit('focus');
    },
  },
};

